<template>
  <div>
    
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Delete",
  created() {
    const params = new URL(document.location).searchParams;
    axios
      .delete(`http://localhost:9999/happyhouse/api/qna/${params.get("no")}`)
      .then(({ data }) => {
        let msg = "  .";
        if (data === "success") {
          msg = " .";
        }
        alert(msg);
        this.$router.push("/list");
      });
  }
};
</script>

