<template>
  <div class="text-align">
    <table>
      <tr>
        <th></th>
        <th>{{ item.qnaTitle }}</th>
      </tr>
      <tr>
        <th></th>
        <th>{{ item.qnaContent }}</th>
      </tr>
      <tr>
        <th></th>
        <th>{{ item.qnaUserid }}</th>
        <th></th>
        <th>{{ getFormatDate(item.qnaDatetime) }}</th>
      </tr>
    </table>

    <div class="text-center">
      <router-link :to="'/list'"
        ><button class="btn btn-primary"></button></router-link
      >
      <router-link :to="'/update?no=' + item.qnaNo"
        ><button class="btn btn-secondary"></button></router-link
      >
      <router-link :to="'/delete?no=' + item.qnaNo"
        ><button class="btn btn-warning"></button></router-link
      >
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
export default {
  name: "Detail",
  data: function() {
    return {
      item: ""
    };
  },
  created() {
    const params = new URL(document.location).searchParams;
    alert(`${params.get("no")}`);
    axios
      .get(`http://localhost:9999/happyhouse/api/qna/${params.get("no")}`)
      .then(({ data }) => {
        console.log(data);
        this.item = data;
      });
  },

  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD HH:mm:ss");
    }
  }
};
</script>
