<template>
  <div
    id="app"
    style="width:375px; height:690px; border:1px solid; padding:0px"
  >
    <div>
      <img
        id="img"
        src="@/img/GG.png"
        style="width:230px; height:120px; margin-top: 90px;"
      />
    </div>

    <div style="margin-top: 90px;">
      <NaverLogin
        class="text-center"
        client-id="o1YdNUHe3Pw0mLoqvsej"
        callback-url="http://i3a401.p.ssafy.io"
        v-bind:is-popup="false"
        v-bind:button-type="3"
        v-bind:button-height="48"
        button-color="green"
        :callbackFunction="callbackFunction"
      />
      <KakaoLogin
        api-key="33a2739cf555e249f8fdf2f60f535d32"
        image="kakao_account_login_btn_medium_narrow"
        :on-success="onSuccess"
        :on-failure="onFailure"
      />
    </div>

    <div>
      <img
        id="img"
        src="@/img/img1.png"
        style="position: absolute; left:70px;top: 570px;"
      />
    </div>
  </div>
</template>

<script>
//style="margin-top: 87px; padding:0px"
import axios from "axios";
import NaverLogin from "vue-naver-login";
import KakaoLogin from "vue-kakao-login";

let onFailure = data => {
  console.log(data);
  console.log("failure");
};

let callbackFunction = status => {
  if (status) {
    /* (5)      callback   */
    var email = NaverLogin.user.getEmail();
    if (email == undefined || email == null) {
      alert(" .  .");
      /* (5-1)         */
      NaverLogin.reprompt();
      console.log(email);
      return;
    }

    window.location.replace(
      "http://" +
        window.location.hostname +
        (location.port == "" || location.port == undefined
          ? ""
          : ":" + location.port) +
        "/sample/main.html"
    );
  } else {
    console.log("callback  .");
  }
};

export default {
  name: "App",
  data() {
    return {};
  },
  components: {
    NaverLogin,
    KakaoLogin
  },
  methods: {
    onSuccess(data) {
      console.log(data);
      console.log("success");
      axios
        .get("http://localhost:9999/happyhouse/api/user/kakao", {
          params: {
            code: data.access_token
          }
        })
        .then(({ data }) => {
          console.log("data : " + data);
          this.$session.set("memberid", data);
          console.log(this.$session.get("memberid"));
          alert(". " + data + "");
          this.$router.push("/riot");
        });
    },
    onFailure,
    callbackFunction
  },
  created() {
    let uri = window.location.href.split("#");
    if (uri.length == 2) {
      let vars = uri[1].split("&");
      let vars2 = vars[0].split("=");
      axios
        .get("http://localhost:9999/happyhouse/api/user/naver", {
          params: {
            code: vars2[1]
          }
        })
        .then(({ data }) => {
          console.log(data);
          this.$session.set("memberid", data.response.email);
          console.log(this.$session.get("memberid"));
          alert(". " + data.response.email + "");
          this.$router.push("/riot");
        });
    }
  }
};
</script>
